@use 'variables';
@use 'mixins';

body {
  line-height: variables.$line-height;
  font-size: variables.$font-size;

  @if variables.$text-adjusting {
    text-size-adjust: variables.$text-size-adjust;
  }

  @if variables.$custom-colors {
    background-color: variables.$background-body;
    color: variables.$text;
  }

  @if variables.$custom-fonts {
    @include mixins.base-font;
  }

  @if variables.$font-smoothing {
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
  }

  text-rendering: optimizeLegibility;
}

// Inlines
a {
  text-decoration: none;
  cursor: pointer;

  @if variables.$custom-colors {
    color: variables.$link;

    @if variables.$hovered-links {
      &:focus, 
      &:hover {
        color: variables.$link-hover;
      }
    }

    @if variables.$visited-links {
      &:visited {
        color: variables.$link-visited;
      }
    }
  }

  @if variables.$show-underline {
    text-decoration: underline;
  } @else {
    &:focus,
    &:hover {
      text-decoration: underline;
    }
  }

  &:active,
  &:hover {
    outline-width: 0;
  }
}

abbr {
  text-decoration: underline dotted;
  cursor: help;
}

cite {
  font-style: italic;
}

hr {
  background: variables.$border;
  border: none;
  display: block;
  height: 1px;
  margin-bottom: variables.$margin;
  margin-top: variables.$margin;
}

img {
  vertical-align: text-bottom;

  @if variables.$round-images {
    border-radius: variables.$image-radius;
  }
}

mark,
ins {
  border-radius: variables.$radius;
  padding: 0em 0.4em 0.2em;
  vertical-align: baseline;
}

ins {
  background-color: variables.$background-ins;
  text-decoration: none;
}

mark {
  background-color: variables.$background-mark;
}

summary {
  display: list-item;
}

details {
  display: block;

  summary {
    cursor: pointer;
  }

  p {
    padding-left: 1.1em;

    &:first-of-type {
      margin-top: 0.25em;
      margin-bottom: 0.25em;
    }
  }
}

small {
  font-size: 0.8em;
}

strong {
  font-weight: variables.$weight-bold;

  @if variables.$custom-colors {
    color: variables.$text-strong;
  }
}

sub,
sup {
  font-size: 0.8em;
}

sub {
  vertical-align: sub;
}

sup {
  vertical-align: super;
}

// Blocks
p,
dl,
ol,
ul,
blockquote,
pre,
table {
  margin-bottom: variables.$margin;

  &:last-child {
    margin-bottom: 0;
  }
}

p {
  &:empty {
    display: none;
  }
}

// Headings
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: variables.$weight-headings;
  line-height: 1.2;

  &:first-child {
    margin-top: 0;
  }

  @if variables.$custom-colors {
    color: variables.$text-headings;
  }
}

h1 {
  @include mixins.heading(2.4);

  line-height: 1.25;
}

h2 {
  @include mixins.heading(1.6, $top-space: true);

  line-height: 1.4;
}

h3 {
  @include mixins.heading(1.3, $top-space: true);
}

h4 {
  @include mixins.heading(1.2, $top-space: true);
}

h5 {
  @include mixins.heading(1.1, $top-space: true);
}

h6 {
  @include mixins.heading(1, $top-space: true);
}

// Paragraphs
@if variables.$closer-lists {
  p {
    & + dl,
    & + ol,
    & + ul {
      margin-top: -(variables.$margin);
    }
  }
}

// Lists
dd {
  margin-left: variables.$margin;
}

ol,
ul {
  list-style-position: outside;
  margin-left: variables.$margin;
}

ol {
  list-style-type: decimal;

  ol {
    list-style-type: lower-alpha;

    ol {
      list-style-type: lower-roman;

      ol {
        list-style-type: lower-greek;

        ol {
          list-style-type: decimal;

          ol {
            list-style-type: lower-alpha;
          }
        }
      }
    }
  }
}

ul {
  list-style-type: disc;

  ul {
    list-style-type: circle;

    ul {
      list-style-type: square;

      ul {
        list-style-type: circle;

        ul {
          list-style-type: disc;

          ul {
            list-style-type: circle;
          }
        }
      }
    }
  }
}

// Input
input,
textarea {
  font: inherit;
  line-height: inherit;

  @if variables.$custom-colors {
    color: variables.$text;
  }

  &::placeholder {
    color: inherit;
    opacity: variables.$opacity;
  }
}

// Blockquote
blockquote {
  border-left: 2px solid variables.$border;
  padding: 0.5em 1em;

  @if variables.$custom-colors {
    color: variables.$text-blockquote;
  }
}

// Code
code,
kbd,
samp,
pre {
  -moz-osx-font-smoothing: auto;
  -webkit-font-smoothing: auto;
  background-color: variables.$background-code;
  color: variables.$text;
  font-size: 0.9em;
  border-radius: variables.$radius;

  @if variables.$custom-fonts {
    @include mixins.code-font;
  }
}

code,
kbd,
samp {
  padding: 0.2em 0.4em 0.2em;

  @if variables.$custom-colors {
    color: variables.$text-code;
  }
}

pre {
  overflow: hidden;

  @if variables.$round-codeblocks {
    border-radius: variables.$codeblock-radius;
  }

  code {
    display: block;
    background: none;
    overflow: auto;
    border: none;
    padding: 1em 1.2em;
    font-size: 1em;
    white-space: pre;
  }
}

// Figure
figure {
  margin-bottom: variables.$margin * 2;
  text-align: center;

  &:first-child {
    margin-top: 0;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

figcaption {
  font-size: 0.8em;
  margin-top: variables.$margin / 1.6;

  @if variables.$custom-colors {
    color: variables.$text-muted;
  }
}

// Table
table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;

  pre {
    white-space: pre-wrap;
  }
}

th,
td {
  @include mixins.cell(1);

  border: 1px solid variables.$border;
  line-height: 1.4;
}

thead,
tfoot {
  tr {
    @if variables.$table-striped {
      background-color: variables.$border;
    } @else {
      background-color: variables.$background-table-hover;
    }
  }

  th,
  td {
    @include mixins.cell(0.9);

    @if variables.$custom-colors {
      color: variables.$table-thead;
    }

    code {
      background-color: variables.$background-table;
    }
  }
}

tbody {
  tr {
    background-color: variables.$background-table;

    @if variables.$table-striped {
      &:nth-child(even) {
        background-color: variables.$background-table-even;
      }

      @if variables.$table-hover {
        &:hover {
          background-color: variables.$background-table-hover;
        }
      }
    } @else {
      @if variables.$table-hover {
        &:hover {
          background-color: variables.$background-table-even;
        }
      }
    }
  }
}
